import styled from '@emotion/styled';


// -------- NAVBAR ----------------
export const navbar = styled.div`
    position: relative;
    height: 10vh;
    width: 100%;
    background-color: rgba(35, 225, 76, 0);
`
export const navbar_moving_container = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    

    ${props => {
        if (props.navbar_magic) {
            return (
            `
            height: 70px;
            position: fixed;
            background-color: #22838c;
            max-width: 1000px;
            top: 1%;
            border-radius: 50px;
            box-shadow: 0px 2px 2px 1px rgba(13, 2, 18, 0.2);
            left: 50%;
            transform: translate(-50%, 0);
            
            -webkit-transition: all ease-out 0.4s;
            -moz-transition: all ease-out 0.4s;
            -o-transition: all ease-out 0.4s;
            transition: height ease-in-out 0.6s, background-color ease 0.2s;
            `
            );
        }
    }}
        
    @media (max-width: 1269px) {
        display: flex;
        
        
    } 
    
`
// -------- LOGO ----------------
export const logo_container = styled.div`
    margin-left: 30px;
    width: 30%;
    
    
`

export const logo = styled.h1`
    font-size: 40px;
    font-family: 'Dancing Script', cursive;
    color: #293856;
    letter-spacing: 1px;
    
    ${props => {
        if (props.navbar_magic) {
            return (
            `
            transition: all .1s ease-in-out;
            color: #fff;
            `
            );
        }
    }}
`

// ----------------ITEMS----------------
export const navbar_items_container = styled.div`
    display: flex;
    color: #293856;
    

    ${props => {
        if (props.navbar_magic) {
            return (
            `
            transition: all .2s ease-in-out;
            color: #fff;
            `
            );
        }
    }}

    @media (max-width: 1269px) {
        position: absolute;
        right: 0;
        
        
     } 
    
    @media (max-width: 830px) {
        display: none;
    } 
`
export const navbar_items = styled.h3`
    margin-right: 30px;

`

// -------- HAMBURGER ----------------
export const navbar_toggler = styled.div`
    @media (max-width: 830px) {
        box-sizing: border-box;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 50px;
        width: 50px;
        right: 30px;
        cursor: pointer;
        transition: all .3s ease-in-out;
    } 
`
export const navbar_burger = styled.div`
@media (max-width: 830px) {
  width: 40px;
  height: 6px;
  background: #293856;
  border-radius: 5px;
  transition: all .2s ease-in-out;
  &:before, &:after {
    content: '';
    position: absolute;
    width: 40px;
    height: 6px;
    background: #293856;
    border-radius: 5px;
    transition: all .2s ease-in-out;
  }
  &:before {
      transform: translateY(-12px);
  }
  &:after {
    transform: translateY(12px);
  }
  
  ${props => {
    if (props.navbar_magic) {
        return (
            `
            background: #fff;
            &:before, &:after { background: #fff; }
            `
        );
    }
  }}
  ${props => {
    if (props.status) {
        return (
            `
            transform: translatex(-50px);
            background: transparent;
            &:before {
                transform: rotate(45deg) translate(35px, -35px);
            }
            &:after {
                transform: rotate(-45deg) translate(35px, 35px);
            }
            `
        );
    }
  }}
}
`
